/* CUSTOM CHECKBOX - RADIO */
.checkbox label:after,
.radio label:after {
    content: '';
    /* display: table; */
    clear: both;
}

.checkbox .cr,
.radio .cr {
    position: relative;
    display: inline-block;
    border: 1px solid #a9a9a9;
    /* border-radius: .25em; */
    width: 25px;
    height: 25px;
    line-height: 1.5;
    margin-right: 1rem;
    text-align: center;
    vertical-align: unset;
    color: #A0A0A0;
}

.radio .cr {
    border-radius: 50%;
}

.checkbox .cr .cr-icon,
.radio .cr .cr-icon {
    position: absolute;
    line-height: 0;
    top: 50%;
    left: 13%;
}

.radio .cr>i {
    margin-left: .5px;
}

.checkbox label,
.radio label {
    margin-bottom: 0px;
}

.checkbox input,
.radio input {
    display: none;
}

.checkbox label input[type="checkbox"]+.cr>i,
.radio label input[type="radio"]+.cr>i {
    opacity: 0;
}

.checkbox label input[type="checkbox"]:checked+.cr>i,
.radio label input[type="radio"]:checked+.cr>i {
    opacity: 1;
}

.checkbox label input[type="checkbox"]:disabled+.cr,
.radio label input[type="radio"]:disabled+.cr {
    opacity: .5;
}

.survey-sidebar a:hover {
    text-decoration: none;
}

@-webkit-keyframes wait {
    0% {
        opacity: 0;
    }

    40% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

@keyframes wait {
    0% {
        opacity: 0;
    }

    40% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes ripple {
    0% {
        box-shadow: 0 0 0 0 rgba(255, 255, 355, 0.3), 0 0 0 .6em rgba(255, 255, 355, 0.3);
    }

    100% {
        box-shadow: 0 0 0 .4em rgba(255, 255, 355, 0.3), 0 0 0 .8em rgba(255, 255, 355, 0.3), 0 0 0 .9em rgba(101, 255, 120, 0);
    }
}

@keyframes ripple {
    0% {
        box-shadow: 0 0 0 0 rgba(255, 255, 355, 0.3), 0 0 0 .6em rgba(255, 255, 355, 0.3);
    }

    100% {
        box-shadow: 0 0 0 .4em rgba(255, 255, 355, 0.3), 0 0 0 .8em rgba(255, 255, 355, 0.3), 0 0 0 .9em rgba(101, 255, 120, 0);
    }
}

